@-webkit-keyframes anim-popoutin {
  0% {
    width: 0vmin;
    color: white;
    font-size: 0vmin;
    opacity: 0;
  }
  50% {
    width: 135vmin;
    color: white;
    font-size: 8vmin;
    opacity: 1;
  }
  100% {
    /* animate nothing to add pause at the end of animation */
    font-size: 6vmin;
    opacity: 1;
  }
}
@keyframes anim-popoutin {
  0% {
    width: 0vmin;
    color: white;
