  }
  100% {
    /* animate nothing to add pause at the end of animation */
    font-size: 6vmin;
    opacity: 1;
  }
}/*# sourceMappingURL=Snusboks.css.map */
.Container {
  -webkit-animation: FadeInnOut 4s ease-in-out;
          animation: FadeInnOut 4s ease-in-out;
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Container h1 {
  font-size: 75px;
  color: white;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}
